@import "../variables";

#Portfolio {
    .portfolio-items {
        margin:0 15px;

        > .portfolio-thumb {
            border-radius:$Border_Radius_Default;
            overflow:hidden;
            cursor:pointer;
        }

        > .portfolio-description {
            > .portfolio-name {
                color:#efefef;
                font-size:1.4rem;
                cursor:text;
                margin:25px 0 0;
            }

            > .portfolio-desc-items {
                margin: 12px 0 0;
                cursor:text;

                > b {
                    margin-right:4px;
                }
            }
        }
    }
}

.portfolio-detail {
    h4 {
        font-weight:500;
        color:#666;
        margin:0;
    }
    h3 {
        font-size:2rem;
        margin:0.5rem 0 1rem;
        word-break: keep-all;
    }

    ul {
        margin-bottom:30px;
    }

    p:last-child {
        margin-bottom:0;
    }
   
}

.portfolio-detail-image {
    border-top:1px solid #ccc;
    padding-top:30px;
}

@media screen and (max-width:393px) {
    .portfolio-detail {
        h4 {
            font-size:0.8rem;
        }

        h3 {
            font-size:1.2rem;
        }

        ul {
            flex-direction: column;
            gap:15px;
            li {
                margin:0
            }
        }
    }
}