@import "./variables";

/* Google Web Fonts CDN */
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:300,400,500,700,900&display=swap');
// font-family: 'Noto Sans KR', sans-serif;

/* Google Web Font */
@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap');
// font-family: 'Montserrat', sans-serif;

/* Noonnu Web Font */
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

* {box-sizing:border-box;}
html, body {width:100%; height:100%;}
body {font-size:16px; line-height:1.7; color:#333;	letter-spacing:-.04em; position:relative; font-family: "Montserrat", sans-serif;}
a, a:hover, a:focus {color:inherit; text-decoration:none; background-color:transparent;}
ul, li {list-style:none; margin:0; padding:0;}


::-webkit-scrollbar {background-color:#000;width:6px;height:6px;border-radius:8px;}
::-webkit-scrollbar-thumb {background-color:$Main_Color;height:10px;border-radius:8px;}
::-webkit-scrollbar-button {display:none;}

.wd-100 {width:100%;}
.hght-100 {height:100%;}

.container {width:100%; margin:0 auto; padding:0 1rem;}

.badge {border-radius:4px; padding:3px 7px; font-weight:600; font-size:0.8rem;}
.badge-default {background-color:#bbb;}
.badge-orange {background-color:#e14e1d; color:#fff;}
.badge-blue {background-color:#0477bd; color:#fff;}
.badge-pink {background-color:#cd6799; color:#fff;}
.badge-yellow {background-color:#f0db4e; color:#32332f;}
.badge-red-orange {background-color:#ff3104; color:#fff;}
.badge-purple {background-color:#7738f7; color:#fff;}
.badge-dark-blue {background-color:#011d37; color:#31a8ff;}
.badge-brown {background-color:#011d37; color:#ff9a02;}



@media screen and (min-width:900px) {
    .container {max-width:1168px;}
}

@media screen and (min-width:1440px) {
    .container {max-width:1468px;}
}

@mixin blockFlexAlign($alignItems, $justifyContent) {
    display:flex;
    align-items:$alignItems;
    justify-content:$justifyContent;
}

@mixin inlineFlexAlign($alignItems, $justifyContent) {
    display:inline-flex;
    align-items:$alignItems;
    justify-content:$justifyContent;
}

@mixin flexCenter {
    display:flex;
    align-items:center;
    justify-content:center;
}

.display-flex {
    display:flex;
}

.display-inline-flex {
    display:inline-flex;
}

.align-items-center {
    align-items:center;
}

.justify-content-between {
    justify-content:space-between;
}

.flex-align-center {
    @include flexCenter
}

.flex-align-center-between {
    @include blockFlexAlign(center, space-between);
}

.flex-align-center-left {
    @include blockFlexAlign(center, flex-start);
}

.flex-align-center-right {
    @include blockFlexAlign(center, flex-end);
}

.inline-flex-align-center-between {
    @include inlineFlexAlign(center, space-between);
}

.inline-flex-align-center-left {
    @include inlineFlexAlign(center, flex-start);
}

.flex-1 {
    flex:1;
}

.flex-2 {
    flex:2;
}

.gap-5 {
    gap:5px;
}

.gap-15 {
    gap:15px;
}

.gap-30 {
    gap:30px;
}

.flex-direction-column {
    flex-direction: column;
}

.gradient-bg {
    position:absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;

    &.gradient-dark-bg {
        background:rgba(0,0,0,0.6);
    }
}

.gradient-edge-top::before {
    content:"";
    display:block;
    position:absolute;
    z-index:1;
    top:0;
    left:0;
    width:100%;
    height:30%;
    background:linear-gradient(180deg, rgba(23, 26, 29, 1) 0%, rgba(23, 26, 29, 0) 100%);
}

.gradient-edge-bottom::after {
    content:"";
    display:block;
    position:absolute;
    z-index:1;
    bottom:0;
    left:0;
    width:100%;
    height:30%;
    background:linear-gradient(0deg, rgba(23,26,29,1) 0%, rgba(23,26,29,0) 100%);
}

#FloatLeft {
    position:fixed;
    z-index:2;
    margin-left:30px;
    top:50%;
    transform: translate3d(0, -50%, 0);
    color:#fff;

    .sns-icons {
        @include flexCenter;
        flex-direction:column;
        gap:5px;
    }

    .sns-icons > li > a {
        display:block;
        font-size:1rem;
        color:$Main_Color;
    }

    .sns-icons > li:last-child {
        writing-mode: vertical-lr;
        transform:rotate(-180deg);
        line-height:1;
        font-size:0.8rem;
        margin-top:15px;
        color:rgba(255, 255, 255, .5);
    }
}

.sections {
    position:relative;
    background-color:#171A1D;
    color:#fff;

    > .container {
        position:relative;
        z-index:2;
    }

    .section-title {
        text-transform:uppercase;
        font-size:2.4rem;
        margin:0 0 50px;
        text-align:center;
        font-family:$EN_Font_Family;
        > .section-title-divider {
            width:160px;
            margin:0 auto;
            gap:10px;
            > li {
                flex:1;
                width:100%;
                height:1px;
                display:inline-block;
                background: #fff;
                &.active {
                    background-color:$Main_Color;
                }
            }
        }
    }
    &:not(#Intro) {
        padding:5rem 0;
    }
}

.img-responsive {
    width:100%;
    display:block;
}

.pagination-bar {
    display:block;
    position: relative;
    width: 100%;
    padding-top: 15px;
    background-color: #efefef;
    border-radius:4px;
}

.pagination-indicator {
    position: absolute;
    width: 0;
    padding-top: 15px;
    top: 0;
    left: 0;
    border-radius:4px;
    display:block;
}

@media screen and (max-width:393px) {
    #FloatLeft {
        display:none;
    }
}