@import "../variables";
#AboutMe {
    color:#fff;

    h3 {
        margin:0 0 15px;
        font-size:1.4rem;
    }
    
    .profile-photo-wrap {
        background-color:#efefef;
        width:360px;
        height:360px;
        border-radius: 50%;
        background:url("../../public/images/4.jpeg")no-repeat center / cover scroll #efefef;
    }

    .profile {
        margin-top:30px;
        display:flex;
        h3 {
            margin:0 0 15px;
            text-transform: uppercase;
        }
        ul {
            display:flex;
            justify-content:space-between;
            > li {
                display:flex;
                align-items:center;
                gap:10px;
                font-size:1.2rem;
                > b {
                    font-size:1rem;
                }
            }
        }
    }

    .flex-align-center-between {
        flex-wrap:wrap;
    }
}

@media screen and (max-width:393px) {
    #AboutMe {
        .flex-align-center-between {
            > div {
                flex-basis:50%;
            }
        }
        
        .profile {
            ul {
                display:block;
            }
        }
    }
}