@import "../variables";

#Header {
    position:fixed;
    z-index:3;
    left:0;
    bottom:0;
    width:100%;
    font-family:'Montserrat', sans-serif;
    padding:2rem 0;

        #Logo {
            font-size:1.4rem;
            color:$Main_Color;
            margin:1rem 0;
            text-transform: uppercase; font-weight:bold; letter-spacing:1px;
    }

    #Nav {
        background-color:#fff;
        transition:all .3s;
        padding:0 2rem;
        border-radius:12px;
        box-shadow:0 8px 16px rgba(0,0,0,0.4);

        > div:nth-of-type(2) {
            position:relative;
            > ul {
                > li.nav-toggle-button {
                    display:none;
                    font-size:1.6rem;
                }
            }
        }

        ul > li > a {
            position:relative;
            text-transform:uppercase;
            font-weight:600;
            opacity:0.65;
            letter-spacing:1px;
            transition:all .3s;

            &.active::after, &::after {
                content:"";
                display:block;
                width:0;
                height:3px;
                background-color:$Main_Color;
                position:absolute;
                bottom:-8px;
                left:50%;
                transform:translateX(-50%);
                transition: all .3s ease-out;
            }
        
            &.active, &:hover {
                opacity:1;
            }

            &.active::after, &:hover::after {
                width:100%;
            }
        }
    }
}

@media screen and (max-width:393px) {
    #Header {
        padding:1rem;
        #Logo {
            font-size:1.2rem;
        }
        #Nav {
            padding:0 1.5rem;
            > div:nth-of-type(2) {
                > ul {
                    > li:not(.nav-toggle-button) {
                        display:none;
                    }

                    > li.nav-toggle-button {
                        display:block;
                    }
                }
            }
        }
    }
}